* {
    margin: 0;
    padding: 0;
    font-family: 'Roboto', sans-serif; 
  }
  
  body {
    background-color: #e6e6e6;
  }
  
  .wrapper {
    width: 800px;
    margin: 30px auto;
  }
  
  .tabs {
    display: flex;
    justify-content: center;
  }
  
  .tabitem {
    border: 2px solid #fff;
    cursor: pointer;
    width: 100px;
    height: 100px;
    text-align: center;
    padding: 15px;
    background-color: #280680;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  
  .tabitem--inactive {
    opacity: 0.3;
    filter: grayscale(100%);
  }
  
  .tabitem__icon {
    color: rgba(255, 255, 255, 0.9);
    font-size: 22px;
  }
  
  .tabitem__title {
    color: rgba(255, 255, 255, 0.9);
    font-size: 16px;
    margin: 10px 0;
  }
  
  .content {
    text-align: center; 
    margin-top: 15px;
    font-size: 18px;
  }